<template>
  <v-container v-if="this.$store.getters.news[this.id]">
    <v-layout row wrap>
      <v-flex xs12>
        <h3 class="mb-3"> </h3>
        <!--Edit title-->
        <p class="mr-2 mb-2">
          
        </p>
          <el-input
            v-model="editedTitle"
            id="title"
            placeholder="( < 100 )"
            :maxlength="100">
          </el-input>
        <!--Edit description-->
        <el-tooltip
          effect="dark"
          placement="top-start">
              <span slot="content">
                 ,     ,<br>
                      .<br>
                      <u>A</u>  . <br>
                     3    - 300 , <br>
                   ,    .
              </span>
          <p class="mt-3">
            
            <v-icon class="mb-1">lightbulb_outline</v-icon>
          </p>
        </el-tooltip>
        <div :id="id">
          <vue-html-editor v-model="editedDescription"></vue-html-editor>
        </div>
      </v-flex>
    </v-layout>

    <!--Close & Save Buttons-->
    <v-layout row wrap>
      <v-flex xs12>
        <v-btn
          @click="$router.push('/admin/news')"
          flat class="error--text">
          
        </v-btn>
        <v-btn
          @click="onSaveChanges"
          :disabled="!isValidForm"
          flat class="success_a--text">
          
        </v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'AdminOneNews',
  props: ['id'],
  data () {
    let oneNews = this.$store.getters.news[this.id] ? this.$store.getters.news[this.id] : {}
    return {
      editDialog: false,
      type: oneNews.type,
      editedTitle: oneNews.title,
      editedDescription: oneNews.description ? oneNews.description : ''
    }
  },
  methods: {
    onSaveChanges () {
      this.editDialog = false
      this.$store.dispatch('updateNewsInfo', {
        newsId: this.id,
        updateData: {
          title: this.editedTitle,
          description: this.editedDescription,
          editDate: new Date()
        }
      })
      this.$router.push('/admin/news')
    }
  },
  computed: {
    isValidForm () {
      return this.editedTitle.trim() && this.editedDescription.trim()
    }
  }
}
</script>

<style scoped>

</style>



